import Player from './player';
import Enemy from './enemy';
import Leaderboard from './leaderboard';
import sound from './sound';

class Gameplay{
  constructor(){
    this.player = new Player();
    this.enemy = new Enemy();
    this.leaderboard = new Leaderboard();
    this.attackSound = new sound('./music/attack.mp3', false, 0.5);
    this.healSound = new sound('./music/heal.mp3', false, 0.5);
  }

  takeDamage(){
    const playerHealtPoints = document.querySelector('.player__helthpoints');
    this.player.healthPoints = this.player.healthPoints - this.player.attack * this.enemy.level;
    this.enemyAttackAnimation();
    this.attackSound.play();
    if(this.player.healthPoints <= 0) {
      this.leaderboard.getInfo();
      const leaderboard = document.querySelector('.leaderboard');
      leaderboard.style.top = 0;
    }

    playerHealtPoints.style.width = `${this.player.healthPoints}%`;
  }

  takeHeal(){
    const playerHealtPoints = document.querySelector('.player__helthpoints');
    this.player.healthPoints = this.player.healthPoints + this.player.attack;
    this.healSound.play();
    this.healAnimation();
    if(this.player.healthPoints >= 100){
      this.player.healthPoints = 100;
    }

    playerHealtPoints.style.width = `${this.player.healthPoints}%`;
  }

  takeDamageFromPlayer(){
    const enemyHealthpoints = document.querySelector('.enemy__healthpoints');
    this.enemy.helthPoints = this.enemy.helthPoints - 20;
    this.attackSound.play();
    this.playerAttackAnimation();
    if(this.enemy.helthPoints <= 0){
      this.enemy.helthPoints = 100;
      this.enemy.setLevelUp();
      const enemyLevel = document.querySelector('.battleground__level');
      enemyLevel.innerHTML = `Раунд ${this.enemy.level}`;
      const enemySide = document.querySelector('.enemy-side');
      enemySide.innerHTML = '';
      this.enemy.renderEnemyInfo();
      this.enemy.renderEnemyAvtar();
    }
    enemyHealthpoints.style.width = `${this.enemy.helthPoints}%`;
  }

  healAnimation(){
    const playerBody = document.querySelector('.avatar__body');
    playerBody.style.animation = 'healAnimation 3s';

    setTimeout(() => {
      playerBody.style.animation = 'none';
    }, 3000);
  }

  playerAttackAnimation(){
    const playerWeapon = document.querySelector('.avatar__arm');
    playerWeapon.style.animation = 'attackPlayer 1s';

    setTimeout(() => {
      playerWeapon.style.animation = 'none';
    }, 1000);
  }

  enemyAttackAnimation(){
    const enemyWeapon = document.querySelector('.enemy__arm');
    enemyWeapon.style.animation = 'attack 1s';

    setTimeout(() => {
      enemyWeapon.style.animation = 'none';
    }, 1000);
  }
}

export default Gameplay;