import Tasks from './tasks-list';

class Abilities{
  constructor(){
    this.tasks = new Tasks();
  }
  renderEmptyModal(){
    const root = document.querySelector('#root');

    const modal = document.createElement('div');
    modal.setAttribute('class', 'modal__abilities');
    modal.innerHTML = `
      <div class="modal fade" id="abilitiesModal" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Выберети задание</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body" id="modal-abilities__body">
          </div>
        </div>
      </div>
    </div>
    `;
    root.appendChild(modal);
    this.addAttackEventHandler();
  }

  addAttackEventHandler(){
    const healButton = document.querySelector('#heal-btn');
    healButton.addEventListener('click', () => {
      this.renderHealAbilities();
    })
    const attackButton = document.querySelector('#attack-btn');
    attackButton.addEventListener('click', () =>{
      this.renderAttackAbitities();
    })
  }

  renderAttackAbitities(){
    const attackTasks = [
      {
        name: 'Математика',
        id: 'taskMath'
      }
      ,{
        name: 'Вставить необходимый знак',
        id: 'taskInsertOperator'
      }
    ];

    const modalBody = document.querySelector('#modal-abilities__body');
    modalBody.innerHTML = '';

    for(let i = 0; i < attackTasks.length; i++){
      const button = document.createElement('button');
      button.setAttribute('type', 'button');
      button.setAttribute('class', 'btn--abilities');
      button.setAttribute('data-toggle', 'modal');
      button.setAttribute('data-target', '#taskModal');
      button.setAttribute('id', `${attackTasks[i].id}`);
      button.innerHTML = `${attackTasks[i].name}`;

      switch(i){
        case 0:
          button.addEventListener('click', () =>{
            $('#abilitiesModal').modal('hide');
            this.tasks.taskMath();
          });
          break;
        case 1:
          button.addEventListener('click', () =>{
            $('#abilitiesModal').modal('hide');
            this.tasks.taskInsertOperator();
          });
        default:
      }

      modalBody.appendChild(button);
    }
  }

  hideAbilitiesModal(){
    const button = document.querySelector('.btn--abilities');
    button.addEventListener('click', () => {
      $('#abilitiesModal').modal('hide');
    })
  }

  renderHealAbilities(){
    const healTasks = [
      {
        name: 'Русский язык',
        id: 'taskRusLang'
      },{
        name: 'Столицы',
        id: 'taskCapital'
      }
    ];

    const modalBody = document.querySelector('#modal-abilities__body');
    modalBody.innerHTML = '';

    for(let i = 0; i < healTasks.length; i++){
      const button = document.createElement('button');
      button.setAttribute('type', 'button');
      button.setAttribute('class', 'btn--abilities');
      button.setAttribute('data-toggle', 'modal');
      button.setAttribute('data-target', '#taskModal');
      button.setAttribute('id', `${healTasks[i].id}`);
      button.innerHTML = `${healTasks[i].name}`;

      switch(i){
        case 0:
          button.addEventListener('click', () => {
            $('#abilitiesModal').modal('hide');
            this.tasks.taskRusLang();
          });
          break;
        case 1:
          button.addEventListener('click', () => {
            $('#abilitiesModal').modal('hide');
            this.tasks.taskCapital();
          });
          break;
        default:
          
      }
      modalBody.appendChild(button);
    }
  }
}

export default Abilities;