import Player from './player';
import Enemy from './enemy';

class Leaderboard{
  constructor(){
    this.player = new Player();
    this.enemy = new Enemy();
    this.leaderboardArray = [
      {
        name: 'Вася',
        score: 45
      },
      {
        name: 'Коля',
        score: 12
      },{
        name: 'Миша',
        score: 4
      },{
        name: 'Оля',
        score: 6
      },{
        name: 'Катя',
        score: 17
      }
    ]
  }

  renderEmptyTable(){
    const root = document.querySelector('#root');

    const leaderboard = document.createElement('div');
    leaderboard.setAttribute('class', 'leaderboard');
    leaderboard.innerHTML = `
      <div class='leaderboard__inner'>
        <span class='leaderboard__header'>Таблица рекордов</span>
        <span class='leaderboard__result'></span>
        <table id='leaderboard__table'>
        </table>
      </div>
    `;

    root.appendChild(leaderboard);
  }

  getInfo(){
    const playerName = document.querySelector('.player__name').innerHTML;
    const enemyCounter = document.querySelector('.battleground__level').innerHTML;
    const level = enemyCounter.slice(-1);

    const resultObj = {
      name: playerName,
      score: +level - 1
    }

    this.leaderboardArray.push(resultObj);

    const sortedLeader = _.reverse(_.sortBy(this.leaderboardArray, [function(o){ return o.score}]));

    const leaderboardTable = document.querySelector('#leaderboard__table');

    for(let i = 0; i < this.leaderboardArray.length; i++){
      const tr = document.createElement('tr');

      const tableName = document.createElement('td');
      tableName.innerHTML = sortedLeader[i].name;

      const tableScore = document.createElement('td');
      tableScore.setAttribute('class', 'leaderboard__score');
      tableScore.innerHTML = sortedLeader[i].score;

      tr.appendChild(tableName);
      tr.appendChild(tableScore);

      leaderboardTable.appendChild(tr);
    }

    const result = `Игрок ${playerName} победил ${level - 1} монстров`;
    const leaderboardResult = document.querySelector('.leaderboard__result');
    leaderboardResult.innerHTML = result;
  }

}

export default Leaderboard;