import Battleground from './components/battleground';

const userNameForm = document.querySelector('.form-start');
userNameForm.addEventListener('submit', (e) => {
  e.preventDefault();
  const userNameInput = document.querySelector('.form-start__input');
  const name = userNameInput.value;

  const battleground = new Battleground(name);
  battleground.hideNameRequestForm();
  battleground.render();
  battleground.renderEnemyLevel();
});

