class Enemy {
  constructor() {
    this.level = 1;
    this.helthPoints = 100;
  }

  renderEnemyInfo() {
    const enemySide = document.querySelector('.enemy-side');
    const info = document.createElement('div');
    info.setAttribute('class', 'enemy__info battleground__info');

    function random(arr){
      return arr[Math.floor(Math.random() * arr.length)];
    }

    function enemyNameGenerator() {
      const firstName = ['Ужасный', 'Злобный', 'Сопливый', 'Абсолютный', 'Безнадежный', 'Безрассудный', 'Внушительный', 'Горький'];
      const middleName = ['Огр', 'Гном', 'Гоблин', 'Кракен', 'Йети', 'Вампир', ' Землелюд'];
      const lastName = ['Том', 'Макс', 'Дима', 'Василий', 'Константин', 'Курлык'];

      const fullName = `${random(firstName)} ${random(middleName)} ${random(lastName)}`;

      return fullName;
    }

    const enemyName = document.createElement('span');
    enemyName.setAttribute('class', 'enemy__name battleground__name');
    enemyName.innerHTML = enemyNameGenerator();
    info.appendChild(enemyName);

    const healthBar = document.createElement('div');
    healthBar.setAttribute('class', 'health-bar__wrapper');
    info.appendChild(healthBar);

    const enemyHealthPoints = document.createElement('span');
    enemyHealthPoints.setAttribute('class', 'enemy__healthpoints');
    enemyHealthPoints.style.width = `${this.helthPoints}%`;
    healthBar.appendChild(enemyHealthPoints);

    enemySide.appendChild(info);
  }

  renderEnemyAvtar() {
    const enemySide = document.querySelector('.enemy-side');

    const enemyAvatar = document.createElement('div');
    enemyAvatar.setAttribute('class', 'avatar enemy__avatar');

    const enemyAvatarInner = document.createElement('div');
    enemyAvatarInner.setAttribute('class', 'enemy__avatar--inner avatar__inner');

    enemyAvatar.innerHTML = '';

    function random(arr){
      return arr[Math.floor(Math.random() * arr.length)];
    }

    const enemyStyle = [1, 2, 3];

    const enemyHead = document.createElement('div');
    enemyHead.setAttribute('class', 'enemy__head');
    enemyHead.innerHTML = `<img src=\'img/enemy-head-${random(enemyStyle)}.png\' alt=\'enemy head\'>`;

    const enemyBody = document.createElement('div');
    enemyBody.setAttribute('class', 'enemy__body');
    enemyBody.innerHTML = `<img src=\'img/enemy-body-${random(enemyStyle)}.png\' alt=\'enemy body\'>`;

    const enemyLegs = document.createElement('div');
    enemyLegs.setAttribute('class', 'enemy__legs');
    enemyLegs.innerHTML = `<img src=\'img/enemy-leg-${random(enemyStyle)}.png\' alt=\'enemy legs\'>`;

    const enemyArms = document.createElement('div');
    enemyArms.setAttribute('class', 'enemy__arm');
    enemyArms.innerHTML = `<img src=\'img/enemy-weapon-${random(enemyStyle)}.png\' alt=\'enemy weapon\'>`;

    enemySide.appendChild(enemyAvatar);
    enemyAvatar.appendChild(enemyAvatarInner);

    enemyAvatarInner.appendChild(enemyHead);
    enemyAvatarInner.appendChild(enemyBody);
    enemyAvatarInner.appendChild(enemyLegs);
    enemyAvatarInner.appendChild(enemyArms);
  }

  setLevelUp() {
    return this.level += 1;
  }

  getEnemyLevel() {
    return this.level;
  }
}

export default Enemy;
